import { VFC } from 'react';
import { LoaderFunction, MetaFunction, useLoaderData } from 'remix';
import useBlogPost from '~/hooks/useBlogPost';

export const loader: LoaderFunction = (data) => {
  return data.params.slug;
}

const Slug: VFC = () => {
  const slug = useLoaderData<string>()
  const [post] = useBlogPost(slug)

  if (!post) {
    return null;
  }

  return (
    <div className="position-relative border-radius-xl overflow-hidden shadow-lg mb-7">
      <div className="container border-bottom">
        <div className="row py-3">
          <div className="col-lg-4 text-start">
            <p className="lead text-dark pt-1 mb-0">Feature 9</p>
          </div>
          <div className="col-lg-4 mt-1 text-center">
            <span className="badge bg-light text-dark"><i className="fas fa-lock me-1" aria-hidden="true"></i> Screenshot</span>
          </div>
          <div className="col-lg-4 text-end my-auto">
            <a href="../../presentation.html#pricing-soft-ui" className="text-primary icon-move-right">Get the Code
              <i className="fas fa-arrow-right text-sm ms-1" aria-hidden="true"></i>
            </a>
          </div>
        </div>
      </div>
      <div>
        <img className="w-100" src="https://raw.githubusercontent.com/creativetimofficial/public-assets/master/soft-ui-design-system/presentation/sections/page-sections/features/features-9.jpg" alt="features-9" />
      </div>
    </div>
  );
};

export const meta: MetaFunction = () => ({
  title: 'Post',
});

export default Slug;
